.App {
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* to prevent scrolling when touch events */
body {
  height: 100%;
  overflow: hidden;
  width: 100%;
  position: fixed;
}

.start-game {
  margin-top: 40vh;
  display: flex;
  justify-content: center;
}

.end-game {
  display: flex;
  margin-top: 40vh;
  justify-content: center;
}

.play-again-link {
  display: flex;
  margin-top: 5vh;
  justify-content: center;
}
